import React from "react";
import wrapper from "../../../commons/WebComponentWrapper";
import ImageList from "@mui/material/ImageList";

const dispatchedEvents = [];

const SrsMuiImageList = function (props, ref) {
  return (
    <ImageList {...props} theme={props.muiTheme}>
      <slot />
    </ImageList>
  );
};

const styles = `
  /* Custom styles for the MuiImageList component */
`;

const props = [
  {
    name: "cols",
    defaultValue: 2,
    type: "number",
  },
  {
    name: "rowHeight",
    defaultValue: 180,
    type: "number",
  },
  {
    name: "gap",
    defaultValue: 8,
    type: "number",
  },
  {
    name: "variant",
    defaultValue: "standard",
    type: "string",
  },
  {
    name: "sx",
    defaultValue: {},
    type: "object"
  }
  // Add other props specific to ImageList if needed
];

const slots = [
  {
    name: "default",
    allowed: ["srs-image-list-item"],
  },
];

const WrappedComponent = wrapper(
  React.forwardRef(SrsMuiImageList),
  props,
  "mui"
);
export { WrappedComponent, styles, props, dispatchedEvents, slots };
